ul, li, a{
    list-style: none;
    text-decoration: none;
    color: inherit;
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}
.clear::after{
    clear: both;
    content: '';
    display: block;
}

.nav{
    position: relative;
    text-align: center;
    width: 100%;
    display: block;
    margin-top: 5px;
    box-shadow: 0 0.125rem 0.625rem rgba(90,97,105,.12);
    clear: both;
}
.fixed-nav {
  position: fixed;
}
.nav .logo{
    padding: 0 10px;
    width: 70px;
    float: left;
}
.nav .logo > img {
    width: 100%; 
}
.nav_list>ul>li{
    float: left;
    padding: 5px 10px;
}
.nav .nav_list{
    position: absolute; top: 18px; left: 50%; transform: translateX(-50%);
    color: #5a6169; 
}
.active{
    color: blue;
    font-size: 1.1em;
}
.lg_cash{    
    padding: 20px 0;
    float: right;
    margin-right: 20px;
}
.lg_cash>ul>li{
    font-size: 0.8em;
    color: red;
    cursor: pointer;
}

/* 로그인 시작 */

.logo_img {
    width: 100%;
    height: 25%;
    background-color: rgb(125, 155, 255);
}
.emty {
    width: 100%;
    height: 5%;
}
.lrView {
    padding-top: 40px;
    width: 50%;
    height: 70%;
    float: left;
    box-sizing: border-box;
}
.line {
    margin-top: 10px;
    border-right: 1px solid #aaa;
}
.logo_img .img {
    width: 100%;
    height: 100%;
    background: url("../../img/Images/icSplashLogo@3x.png") center center no-repeat;
    background-size: contain;
    margin: 0 auto;
}

.lrView > .leftItem {
    width: 80%;
    height: 13%;
    margin: 3%;
}

.lrView > .le_bu {
    margin-top: 3%;
    height: 15%;
    background-color: rgb(125, 155, 255);
    border-radius: 10px;
}

.lrView > .rigthItem {
    position: relative;
    width: 80%;
    height: 20%;
    margin: 3%;
    background-color: rgb(125, 155, 255);
    border-radius: 10px;
}
/* 모바일 */
@media( max-width:620px ){
    .nva .logo{
        padding: 0;
    }
    .nav .nav_list{
        left: 48%;
    }
    .nav_list>ul>li{
        font-size: 0.8em;
        font-weight: 600;
        width: 33.33333%;
    }
    .lg_cash{
        margin-top: 9px;
        margin-right: 0;
        padding: 0;
    }
    .lg_cash>ul>li{
        width:100%;
        padding: 0;
    }
}