/*노멀라이즈 시작*/
ol, ul, li, a {
	list-style: none;
    text-decoration: none;
    color: inherit;
}
.clear::after {
    content:'';
    display:block;
    clear:both;
}
/*노멀라이즈 끝*/

/*전체 컨트렉트 페이지 시작*/
.con_wlist{
    height: 100%;
    margin: 0 auto;
    padding: 10px 0 0 0;
    background-color: #f8f9fa;
}
.con_wlist>div{
    float: left;
    margin: 0 2%;
    display: block;
    box-sizing: border-box;
    padding: 10px 0;
}
/*전체 컨트렉트 페이지 끝*/

/*작성페이지 시작*/
.con_wlist .con_w {
    display: block;
    width: auto;
}
#con3_1{
    position: relative;
    min-width: 460px;
    max-width: 460px;
    width: 100%;
    height: 600px;
    background: #fff url("../img/main_2.jpg") center center no-repeat;
    background-size: contain;
    box-shadow: 
        0 0.46875rem 2.1875rem rgba(90,97,105,.1),
        0 0.9375rem 1.40625rem rgba(90,97,105,.1),
        0 0.25rem 0.53125rem rgba(90,97,105,.12),
        0 0.125rem 0.1875rem rgba(90,97,105,.1);
    border-radius: 10px;
}
.title_name {
    color: #495057;
    padding: 10px 0;
    font-size: 2em;
}
#form>div{
    position: absolute;
}
#form>div>input{
    font-size: 0.7em;
    color: #343a40;
    height: 10px;
}
#form>div:nth-child(1){
    top: 21.5%;
    left: 17%;
}
#form>div:nth-child(1)>input{
    width: 60px;
}
#form>div:nth-child(2){
    top: 21.5%;
    left: 56%;
}
#form>div:nth-child(2)>input{
    width: 60px;
}
#form>div:nth-child(3){
    top: 32.5%;
    left: 25.5%;
}
#form>div:nth-child(3)>input{
    width: 70px;
    height: 13px;
}
#form>div:nth-child(4){
    top: 32.5%;
    left: 46.5%;
}
#form>div:nth-child(4)>input{
    width: 70px;
    height: 13px;
}
#form>div:nth-child(5){
    top: 43%;
    left: 30.5%;
}
#form>div:nth-child(5)>input{
    width: 100px;
}
#form>div:nth-child(6){
    top: 46%;
    left: 30.5%;
}
#form>div:nth-child(6)>input{
    width: 100px;
}
#form>div:nth-child(7){
    top: 56.8%;
    left: 60%;
}
#form>div:nth-child(7)>input{
    width: 60px;
}
#form>div:nth-child(8){
    top: 76.5%;
    left: 51%;
}
#form>div:nth-child(8)>input{
    width: 45px;
}
#form>div:nth-child(9){
    top: 79.2%;
    left: 52%;
}
#form>div:nth-child(9)>input{
    width: 70px;
}
#con3_1 .preview{
    position: absolute;
    bottom: 6%;
    left: 63%;
}
#con3_1 .covenant{
    position: absolute;
    bottom: 6%;
    left: 80%;
}

/* 미리보기 시작 */
#con3_1 > div > div > div > div:nth-child(1){
    position: relative;
    min-width: 460px;
    max-width: 460px;
    height: 600px;
    background:url("../img/preview.jpg") center center no-repeat;
    background-size: contain;
}
#con3_1 > div > div > div > div:nth-child(1)>p{
    position: absolute;
    font-weight: bold;
    font-size: 0.6em;
    color: #343a40;
    height: 10px;
}
#employerName{
    top: 21%;
    left: 22%;
}
#employeeName{
    top: 21%;
    left: 55.5%;
}
#contractTerms{
    top: 31.5%;
    left: 29.8%;
}
#contractTerme{
    top: 31.5%;
