body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.conlist{
    width: 100%;
    padding: 0 10px;
    margin-bottom: 10px;
    border: 1px solid #7d9bff;
    border-radius: 5px;
    box-sizing: border-box;
}
.conlist:hover{
    border: 1px solid #fa5252;
    box-shadow:
        0 14px 28px rgba(0,0,0,0.25),
        0 10px 10px rgba(0,0,0,0.22);
}
.conlist>div>span {
    font-size: 0.7em;
}
.conlist>.title>span:last-child {
    margin-left: 5%;
}
/*노멀라이즈 시작*/
ol, ul, li, a {
	list-style: none;
    text-decoration: none;
    color: inherit;
}
.clear::after {
    content:'';
    display:block;
    clear:both;
}
/*노멀라이즈 끝*/

/*전체 컨트렉트 페이지 시작*/
.con_wlist{
    height: 100%;
    margin: 0 auto;
    padding: 10px 0 0 0;
    background-color: #f8f9fa;
}
.con_wlist>div{
    float: left;
    margin: 0 2%;
    display: block;
    box-sizing: border-box;
    padding: 10px 0;
}
/*전체 컨트렉트 페이지 끝*/

/*작성페이지 시작*/
.con_wlist .con_w {
    display: block;
    width: auto;
}
#con3_1{
    position: relative;
    min-width: 460px;
    max-width: 460px;
    width: 100%;
    height: 600px;
    background: #fff url(/react-tt-v1/static/media/main_2.84572123.jpg) center center no-repeat;
    background-size: contain;
    box-shadow: 
        0 0.46875rem 2.1875rem rgba(90,97,105,.1),
        0 0.9375rem 1.40625rem rgba(90,97,105,.1),
        0 0.25rem 0.53125rem rgba(90,97,105,.12),
        0 0.125rem 0.1875rem rgba(90,97,105,.1);
    border-radius: 10px;
}
.title_name {
    color: #495057;
    padding: 10px 0;
    font-size: 2em;
}
#form>div{
    position: absolute;
}
#form>div>input{
    font-size: 0.7em;
    color: #343a40;
    height: 10px;
}
#form>div:nth-child(1){
    top: 21.5%;
    left: 17%;
}
#form>div:nth-child(1)>input{
    width: 60px;
}
#form>div:nth-child(2){
    top: 21.5%;
    left: 56%;
}
#form>div:nth-child(2)>input{
    width: 60px;
}
#form>div:nth-child(3){
    top: 32.5%;
    left: 25.5%;
}
#form>div:nth-child(3)>input{
    width: 70px;
    height: 13px;
}
#form>div:nth-child(4){
    top: 32.5%;
    left: 46.5%;
}
#form>div:nth-child(4)>input{
    width: 70px;
    height: 13px;
}
#form>div:nth-child(5){
    top: 43%;
    left: 30.5%;
}
#form>div:nth-child(5)>input{
    width: 100px;
}
#form>div:nth-child(6){
    top: 46%;
    left: 30.5%;
}
#form>div:nth-child(6)>input{
    width: 100px;
}
#form>div:nth-child(7){
    top: 56.8%;
    left: 60%;
}
#form>div:nth-child(7)>input{
    width: 60px;
}
#form>div:nth-child(8){
    top: 76.5%;
    left: 51%;
}
#form>div:nth-child(8)>input{
    width: 45px;
}
#form>div:nth-child(9){
    top: 79.2%;
    left: 52%;
}
#form>div:nth-child(9)>input{
    width: 70px;
}
#con3_1 .preview{
    position: absolute;
    bottom: 6%;
    left: 63%;
}
#con3_1 .covenant{
    position: absolute;
    bottom: 6%;
    left: 80%;
}

/* 미리보기 시작 */
#con3_1 > div > div > div > div:nth-child(1){
    position: relative;
    min-width: 460px;
    max-width: 460px;
    height: 600px;
    background:url(/react-tt-v1/static/media/preview.1274cd64.jpg) center center no-repeat;
    background-size: contain;
}
#con3_1 > div > div > div > div:nth-child(1)>p{
    position: absolute;
    font-weight: bold;
    font-size: 0.6em;
    color: #343a40;
    height: 10px;
}
#employerName{
    top: 21%;
    left: 22%;
}
#employeeName{
    top: 21%;
    left: 55.5%;
}
#contractTerms{
    top: 31.5%;
    left: 29.8%;
}
#contractTerme{
    top: 31.5%;
    left: 45.5%;
}
#workPlace{
    top: 42.5%;
    left: 33.5%;
}
#workDetail{
    top: 45%;
    left: 33.5%;
}
#workTime{
    top: 55.8%;
    left: 60%;
}
#wage{
    top: 75.5%;
    left: 52%;
}
#payDay{
    top: 78.2%;
    left: 58%;
}
.pre_bt{
    position: absolute;
    top: 3%;
    left: 6%;
}
/* 미리보기 끝 */

/*작성페이지 끝*/

/*리스트 시작*/
.con_wlist .all_con_list {
    display: block;
    width: auto;
}
.all_con_list .con_list{
    background-color: #fff;
    padding: 10px 10px;
    box-shadow: 
        0 0.46875rem 2.1875rem rgba(90,97,105,.1),
        0 0.9375rem 1.40625rem rgba(90,97,105,.1),
        0 0.25rem 0.53125rem rgba(90,97,105,.12),
        0 0.125rem 0.1875rem rgba(90,97,105,.1);
    border-radius: 10px;
}
/*리스트 끝*/

/* 모바일 */
@media( max-width:760px ){
    .con_wlist>div{
        margin: 0;
    }
    .con_wlist .con_w {
        width: 100%;
    }
    .con_wlist .all_con_list {
        width: 100%;
    }
}
.main{
    padding-top: 35px;
    width: 100%;
}
.main .main_img{
    width: auto;
    margin-left: auto;
    margin-right: auto;
}
.main .main_img>img{
    display: block;
    width: 100%;
}

@media(max-width:760px){
    .main .main_img>img{
       min-width: 100%;
       background-size: contain;
    }

}
ul, li, a{
    list-style: none;
    text-decoration: none;
    color: inherit;
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}
.clear::after{
    clear: both;
    content: '';
    display: block;
}

.nav{
    position: relative;
    text-align: center;
    width: 100%;
    display: block;
    margin-top: 5px;
    box-shadow: 0 0.125rem 0.625rem rgba(90,97,105,.12);
    clear: both;
}
.fixed-nav {
  position: fixed;
}
.nav .logo{
    padding: 0 10px;
    width: 70px;
    float: left;
}
.nav .logo > img {
    width: 100%; 
}
.nav_list>ul>li{
    float: left;
    padding: 5px 10px;
}
.nav .nav_list{
    position: absolute; top: 18px; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%);
    color: #5a6169; 
}
.active{
    color: blue;
    font-size: 1.1em;
}
.lg_cash{    
    padding: 20px 0;
    float: right;
    margin-right: 20px;
}
.lg_cash>ul>li{
    font-size: 0.8em;
    color: red;
    cursor: pointer;
}

/* 로그인 시작 */

.logo_img {
    width: 100%;
    height: 25%;
    background-color: rgb(125, 155, 255);
}
.emty {
    width: 100%;
    height: 5%;
}
.lrView {
    padding-top: 40px;
    width: 50%;
    height: 70%;
    float: left;
    box-sizing: border-box;
}
.line {
    margin-top: 10px;
    border-right: 1px solid #aaa;
}
.logo_img .img {
    width: 100%;
    height: 100%;
    background: url(data:image/png;base64,) center center no-repeat;
    background-size: contain;
    margin: 0 auto;
}

.lrView > .leftItem {
    width: 80%;
    height: 13%;
    margin: 3%;
}

.lrView > .le_bu {
    margin-top: 3%;
    height: 15%;
    background-color: rgb(125, 155, 255);
    border-radius: 10px;
}

.lrView > .rigthItem {
    position: relative;
    width: 80%;
    height: 20%;
    margin: 3%;
    background-color: rgb(125, 155, 255);
    border-radius: 10px;
}
/* 모바일 */
@media( max-width:620px ){
    .nva .logo{
        padding: 0;
    }
    .nav .nav_list{
        left: 48%;
    }
    .nav_list>ul>li{
        font-size: 0.8em;
        font-weight: 600;
        width: 33.33333%;
    }
    .lg_cash{
        margin-top: 9px;
        margin-right: 0;
        padding: 0;
    }
    .lg_cash>ul>li{
        width:100%;
        padding: 0;
    }
}
