.conlist{
    width: 100%;
    padding: 0 10px;
    margin-bottom: 10px;
    border: 1px solid #7d9bff;
    border-radius: 5px;
    box-sizing: border-box;
}
.conlist:hover{
    border: 1px solid #fa5252;
    box-shadow:
        0 14px 28px rgba(0,0,0,0.25),
        0 10px 10px rgba(0,0,0,0.22);
}
.conlist>div>span {
    font-size: 0.7em;
}
.conlist>.title>span:last-child {
    margin-left: 5%;
}